import React from 'react';
import { HashRouter as Router, Route } from "react-router-dom";
import { Blog, Home } from "./pages";
import { GlobalStyles, Navigation } from "./components";
import { useLocalStorage } from "react-use";
import { ThemeProvider } from 'emotion-theming';
import { ThemeContext } from "./context";
import * as themes from './theme';
import { useDarkModeEnable } from './hooks';

const getTheme = theme => themes[theme] || themes.light;

const App = () => {
  const [themeKey, themeKeySet] = useLocalStorage('theme', 'system');
  const [enabled, systemTheme] = useDarkModeEnable();
  const theme = (themeKey === "system") ?
    (
      enabled ? getTheme(systemTheme) : getTheme('light')
    )
    : getTheme(themeKey);

  return (
    <ThemeContext.Provider value={{ theme: themeKey, setTheme: themeKeySet }}>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <Router>
          <Navigation />
          <Route component={Home} path="/" exact />
          <Route component={Blog} path="/blog" />
        </Router>
      </ThemeProvider>
    </ThemeContext.Provider>
  );
}

export default App;
