import React, { useContext } from 'react';
import styled from '@emotion/styled';
import Sticky from 'react-stickynode';
import { ThemeContext } from "../context";
import { useDarkModeEnable } from '../hooks';
import { Link, useRouteMatch } from "react-router-dom";
import c from "classnames";
import Icon from '@mdi/react'
import { mdiBrightnessAuto as mdiAuto, mdiBrightness5 as mdiLight, mdiBrightness7 as mdiDark } from '@mdi/js'


const NavigationWrapper = styled.nav`
    display: flex;
    background: ${({ theme }) => theme.colors.navbar};
    height: ${({ theme }) => theme.sizes.navbar.height};
    transition: color 1s, background 1s;
    font-size: 16px;

    &.hide-top {
        background: transparent;
        color: white;

        .active & {
            background: ${({ theme }) => theme.colors.navbar};
            color: inherit;
        }
    }

    .title {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 1rem;
    }

    h1 {
        margin: 0
    }

    .urls {
        align-items: center;
        display: flex;
        justify-content: center;
        margin-right: auto;
        padding: 0;

        li {
        display: inline-block;
            a {
                margin-right: 1rem;
            }
        }
    }

    .theme-switcher {
        display: flex;
        align-items: center;
    }
`;

const mapThemeKeyToIcon = {
    'system': mdiAuto,
    'light': mdiLight,
    'dark': mdiDark
}

const mapThemeKeyToText = {
    'system': 'Auto',
    'light': 'Light',
    'dark': 'Dark'
}

const ThemeButtonWrapper = styled.button`
    margin: 0 0.5rem;
    padding: 0;
    background: transparent;
    border: none;
    outline: none;

    .icon {
        & > path {
            fill: ${({ theme }) => theme.colors.text};
        }

        .hide-top & > path {
            fill: white;
        }

        .active .hide-top & > path  {
            fill: ${({ theme }) => theme.colors.text};
        }

        & > path {
            opacity: 0.5;
            transition: opacity .5s, fill .5s;
        }
        &.active > path {
            opacity: 1;
        }
    }


`;

const Navigation = () => {
    const { theme, setTheme } = useContext(ThemeContext);
    const [enabled] = useDarkModeEnable();
    const isHome = useRouteMatch({
        path: '/',
        exact: true,
    });

    return <Sticky enabled={true} top={0} bottomBoundary={1200} innerZ={1}>
        <NavigationWrapper className={c({ 'hide-top': !!isHome })}>
            <div className="title">
                <Link to="/">xantiagoma</Link>
            </div>
            <ul className="urls">
                {/* <li><Link to="/blog">Blog</Link></li> */}
            </ul>
            <div className="theme-switcher">
                {
                    [
                        'light',
                        enabled && 'system',
                        'dark',
                    ].filter(e => e).map(themeKey =>
                        <ThemeButtonWrapper key={themeKey} onClick={() => setTheme(themeKey)}>
                            <Icon path={mapThemeKeyToIcon[themeKey]}
                                title={mapThemeKeyToText[themeKey]}
                                size="25px"
                                className={c('icon', { 'active': theme === themeKey })}
                                color=""
                            />
                        </ThemeButtonWrapper>

                    )
                }
            </div>
        </NavigationWrapper>
    </Sticky>;
}

export default Navigation;