import { useState, useEffect } from 'react';
import { useWindowSize, useOrientation } from "react-use";


export function useDarkModeEnable() {
    const [state, stateSet] = useState([false]);

    const handler = () => {
        if (window.matchMedia) {
            if (window.matchMedia('(prefers-color-scheme: dark)').matches) {
                return [true, 'dark'];
            } else {
                return [true, 'light'];
            }
        } else {
            return [false];
        }
    }

    useEffect(() => {
        stateSet(handler());
        window.matchMedia &&
            window.matchMedia('(prefers-color-scheme: dark)').addEventListener &&
            window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', () => stateSet(handler()));
    }, []);

    return state;
}


export function useScreenOrientation() {
    const { type: orientation } = useOrientation();
    const { width, height } = useWindowSize();
    const orientationSupported = !!window.screen.orientation;
    const calcOrientation = width > height ? 'landscape' : 'portrait';
    return orientationSupported ? orientation : calcOrientation;
}