import produce from "immer"
import { mapValues } from "lodash";

const breakpoints = {
    xs: 30,
    sm: 48,
    md: 64,
    lg: 75
};

const _mediaqueries = mapValues(breakpoints,
    bp => `@media only screen and (min-width: ${bp}em)`
)

const mediaqueries = produce(_mediaqueries, draft => {
    draft.se = `@media (device-height : 568px)
    and (device-width : 320px)
    and (-webkit-min-device-pixel-ratio: 2)`;
})

const base = {
    name: 'base',
    colors: {
        primary: 'hotpink',
        background: '#000000',
    },
    sizes: {
        navbar: {
            height: '50px',
        }
    },
    mediaqueries
}

const light = produce(base, draft => {
    draft.name = "light";
    draft.colors.navbar = "#e8eaf3";
    draft.colors.container = "#F5F6FA";
    draft.colors.background = "#FFFFFF";
    draft.colors.text = "#0E0E0E";
});

const dark = produce(base, draft => {
    draft.name = "dark";
    draft.colors.background = '#070706';
    draft.colors.container = "#0E0E0E";
    draft.colors.navbar = "#000000";
    draft.colors.text = "#F5F6FA";
});

export {
    light,
    dark,
    mediaqueries
}